:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1300px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-auto-rows: 360px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.photo-card {
  flex-direction: column;
  display: flex;
}

.info {
  display: flex;
}

.search-form {
  width: 100%;
  height: 3.5rem;
  background-color: #303094;
  justify-content: center;
  align-items: center;
  margin-bottom: 12px;
  display: flex;
}

.search-input, .submit-button {
  height: 2rem;
  border: none;
  outline: none;
}

.search-input {
  width: 25%;
  border-radius: 3px 0 0 3px;
  padding: 0 10px;
}

.submit-button {
  width: 2rem;
  border-radius: 0 3px 3px 0;
}

.gallery__item {
  color: #000;
  border-radius: 0 0 5px 5px;
  text-decoration: none;
  box-shadow: 0 0 2px #000;
}

.gallery__item img {
  height: 80%;
  object-fit: cover;
  width: 100%;
}

.info {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.lds-ring {
  width: 80px;
  height: 80px;
  margin: auto;
  display: inline-block;
  position: relative;
}

.lds-ring div {
  box-sizing: border-box;
  width: 64px;
  height: 64px;
  border: 8px solid rgba(0, 0, 0, 0);
  border-top-color: #000;
  border-radius: 50%;
  margin: 8px;
  animation: lds-ring 1.2s cubic-bezier(.5, 0, .5, 1) infinite;
  display: block;
  position: absolute;
}

.lds-ring div:nth-child(1) {
  animation-delay: -.45s;
}

.lds-ring div:nth-child(2) {
  animation-delay: -.3s;
}

.lds-ring div:nth-child(3) {
  animation-delay: -.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

